<template>
  <div class="setup">
    <div class="set">
      <div>代理IP:</div>
      <textarea name="" id="" cols="30" rows="10" v-model="addip"></textarea>
      <div class="btn" @click="submin">提交</div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      addip:"",
      iplist:[],
      page:2,
      num:10,
    }
  },
  methods: {
    submin(){
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://interfa.tuxiansheng.top/api";
      let data={
        "action": "add_proxy",
        "data": {
            "ip": this.addip
        }
      }
      if(this.addip==""){
        this.$message.error("代理ip不能为空");
        return;
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==0){
          this.$message({
            message: '提交成功',
            type: 'success'
          });
          this.getip();
        }else{
          this.$message.error("提交失败")
        }
      })
    },
    getip(){
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://interfa.tuxiansheng.top/api";
      let data={
        "action": "list_proxy",
        "data": {
            "page": this.page,
            "num": this.num
        } 
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        console.log(res.data.data);
        let ip="";
        for(let a=0;a<res.data.data.length;a++){
          ip+=res.data.data[a].ip+'\n';
        }
        console.log(ip)
        this.addip=ip;

      })
    },
  },
  created(){
    this.getip();
  }
}
</script>
<style>
  .setup{
    padding:50px;
    display: flex; 
  }
  textarea{
    width: 275px;
    height: 240px;
    line-height: 20px;
    color: rgba(136, 136, 136, 1);
    font-size: 13px;
    text-align: left;
    font-family: Microsoft Yahei;
    border: 1px solid rgba(187, 187, 187, 1);
    resize: none;
    margin:10px 0px;
    outline: none;
    line-height: 20px;
  }
  .btn{
    width: 80px;
    height: 25px;
    line-height: 25px;
    text-align: center;
    border: 1px solid rgba(187, 187, 187, 1);
    border-radius:5px;
    cursor: pointer;
  }
  .iplist{
    margin-right:50px;
  }
  .set-result{
    width: 300px;

  }
  ul{
    list-style: none;
    margin:20px;
  }
  li{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom:1px solid #000;
    margin-bottom: 13px;
  }
  li img{
    width: 20px;
    cursor: pointer;
  }
</style>


